body {
  font: 16px sans-serif;
  color: #424242; }

h1 {
  font-family: sans-serif; }

html,
body {
  height: 100%; }

body {
  background: #eee; }

main {
  background: white; }

.container {
  width: 90vw;
  margin-left: auto;
  margin-right: auto;
  padding: 1em; }
  @media (min-width: 48em) {
    .container {
      width: 48em; } }
  @media (min-width: 62em) {
    .container {
      width: 62em; } }
  @media (min-width: 75em) {
    .container {
      width: 75em; } }

body > header {
  padding-top: 2em;
  padding-bottom: 2em; }

header > nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center; }

header > nav li {
  display: inline-block; }

header > nav a {
  text-decoration: none;
  display: block;
  padding: .5em 1em;
  color: #888;
  font-weight: bold;
  font-size: 18px; }
  header > nav a:hover {
    color: #424242; }

header > nav li.active a {
  color: #424242; }

.input__group {
  margin-top: 2em;
  margin-bottom: 2em;
  display: flex;
  align-items: flex-start; }

.button__group {
  text-align: right; }

label {
  flex: 1;
  text-align: right;
  padding-left: 1em;
  padding-right: 1em; }

input {
  flex: 4;
  border: 0;
  background: #eff1f3;
  font-size: 16px;
  line-height: 2em;
  padding-left: .5em;
  padding-right: .5em; }

textarea {
  flex: 4;
  border: 0;
  background: #eff1f3;
  font-size: 16px;
  line-height: 2em;
  padding: .25em .5em; }

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  font-size: .85em;
  line-height: 1.5em;
  color: #424242;
  opacity: .6; }

input:-moz-placeholder,
textarea:-moz-placeholder {
  /* Firefox 18- */
  font-size: .85em;
  line-height: 1.5em;
  color: #424242;
  opacity: .6; }

input::-moz-placeholder,
textarea:-moz-placeholder {
  /* Firefox 19+ */
  font-size: .85em;
  line-height: 1.5em;
  color: #424242;
  opacity: .6; }

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  font-size: .85em;
  line-height: 1.5em;
  color: #424242;
  opacity: .6; }

.button {
  border: none;
  padding: 1em 2em;
  background: #398d9c;
  color: white; }
  .button:hover {
    background: #33666f; }

body > footer {
  padding-top: 2em;
  padding-bottom: 2em;
  text-align: center; }
